@charset "UTF-8";
/**
* remに対応していないブラウザ用
*/
/**
* 絶対位置で配置する
*/
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html {
  font-size: 62.5%; }

body {
  font-size: 16px;
  font-size: 1.6rem;
  -webkit-text-size-adjust: 100%;
     -moz-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "\6E38\30B4\30B7\30C3\30AF\4F53", YuGothic, "\6E38\30B4\30B7\30C3\30AF   Medium", "Yu Gothic Medium", "\6E38\30B4\30B7\30C3\30AF", "Yu Gothic", "\30E1\30A4\30EA\30AA", sans-serif;
  font-feature-settings: "palt";
  background-color: #fff; }

a {
  color: #000;
  text-decoration: none; }
  a:hover {
    color: #000;
    text-decoration: underline; }

.Contents {
  position: relative;
  min-height: 100vh;
  padding-top: 56px; }

.SectionWrapper > section {
  margin: 0 auto; }

.GlobalHeader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  width: 100%;
  height: 56px;
  background-color: #fff;
  z-index: 999;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.1); }
  .GlobalHeader__logo a {
    display: block;
    width: 100%;
    height: 100%; }
  .GlobalHeader__nav {
    font-weight: 700; }
    .GlobalHeader__nav ul {
      display: flex;
      align-items: center; }

.GlobalFooter {
  position: relative;
  background-color: #000;
  color: #fff;
  display: flex;
  align-items: center; }
  .GlobalFooter__links {
    display: flex; }
    .GlobalFooter__links li a {
      color: #fff; }
      .GlobalFooter__links li a:hover {
        color: #fff; }
  .GlobalFooter__copyright {
    font-family: "Roboto", sans-serif;
    font-weight: 700; }

.SectionBasic {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .SectionBasic.gray {
    background-color: #f4f4f4; }
  .SectionBasic__content {
    width: 100%; }
    .SectionBasic__content > * {
      flex: 1; }

.SectionMain {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: #ed1c23; }
  .SectionMain__content {
    display: flex;
    justify-content: center; }
  .SectionMain__btn {
    margin-top: 32px; }

.SectionProduct__img {
  width: 100%; }
  .SectionProduct__img:before {
    content: "";
    display: block;
    padding-top: 48%;
    max-height: 640px; }

.SectionProduct__meta {
  display: flex;
  align-items: flex-start; }

.SectionProduct__content {
  flex: 1; }

.EditableContent {
  line-height: 2;
  letter-spacing: 2px; }
  .EditableContent.concept {
    font-weight: 700; }
  .EditableContent * + p {
    margin-top: 1.5em; }

.ProductItemList {
  display: grid; }
  .ProductItemList.col1 {
    grid-template-columns: 1fr; }
  .ProductItemList.col3 {
    grid-template-columns: repeat(3, 1fr); }

.ProductItem {
  position: relative;
  display: flex;
  flex-direction: column; }
  .ProductItem a:hover {
    text-decoration: none; }
  .ProductItem__img {
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center; }
    .ProductItem__img:before {
      content: "";
      display: block;
      padding-top: 56.25%; }
    .ProductItem__img a {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; }
  .ProductItem__img + .ProductItem__info {
    margin-top: 8px; }
  .ProductItem__info {
    display: flex;
    flex-direction: column;
    line-height: 1.666;
    font-weight: 700; }
  .ProductItem__main {
    display: flex;
    flex-direction: column; }
  .ProductItem__name {
    font-size: 18px;
    font-size: 1.8rem; }
  .ProductItem__copy {
    font-size: 12px;
    font-size: 1.2rem;
    color: #777; }
  .ProductItem__price {
    font-size: 14px;
    font-size: 1.4rem;
    margin-top: 20px; }

.Btn {
  display: inline-block;
  white-space: nowrap;
  text-align: center;
  font-weight: 700; }
  .Btn[type="submit"] {
    border: none; }
  .Btn:hover {
    text-decoration: none; }
  .Btn.round {
    border-radius: 9999px; }
  .Btn.red {
    background-color: #ed1c23;
    color: #fff; }
  .Btn:hover {
    text-decoration: none; }

.SectionTitle {
  display: flex;
  flex-direction: column;
  text-align: center;
  line-height: 1.333; }
  .SectionTitle__jp {
    font-weight: 700;
    font-size: 20px;
    font-size: 2rem; }
  .SectionTitle__en {
    font-size: 12px;
    font-size: 1.2rem;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    color: #ed1c23; }

.ProductInfo__main {
  display: flex;
  flex-direction: column;
  font-weight: 700;
  line-height: 1.5; }

.ProductInfo__name {
  font-size: 24px;
  font-size: 2.4rem; }

.ProductInfo__price {
  font-size: 16px;
  font-size: 1.6rem; }

.ProductInfo__selects {
  display: flex;
  flex-direction: column; }

.ProductInfo__foot {
  display: flex;
  flex-direction: column;
  line-height: 2;
  font-size: 12px; }

.ProductInfo__note {
  line-height: 2;
  color: #777;
  font-size: 12px;
  font-size: 1.2rem; }

.Slideshow {
  background-color: #fefefe;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: all linear 0.3s; }

@media (min-width: 920px) {
    .GlobalHeader {
      padding: 0 24px; }
    .GlobalHeader__logo {
      width: 54px;
      height: 35px; }
    .GlobalHeader__logo + .GlobalHeader__nav {
      margin-left: 32px; }
        .GlobalHeader__nav ul li + * {
          margin-left: 32px; }
    .GlobalFooter {
      justify-content: space-between;
      height: 60px;
      font-size: 12px;
      font-size: 1.2rem;
      padding: 0 16px; }
      .GlobalFooter__links {
        align-items: center; }
      .GlobalFooter__links li + * {
        margin-left: 32px; }
    .SectionBasic {
      padding: 56px 20px 112px; }
    .SectionTitle + .SectionBasic__content {
      margin-top: 24px; }
    .SectionMain {
      padding: 80px 20px 112px; }
      .SectionMain__content {
        margin-top: 56px; }
  .SectionProduct {
    max-width: 1024px;
    width: 100%;
    padding: 80px 20px 120px; }
  .SectionProduct__img + .SectionProduct__meta {
    margin-top: 64px; }
    .SectionProduct__content {
      padding-left: 32px; }
      .ProductItemList.col1 {
        max-width: 640px;
        gap: 80px; }
      .ProductItem__info {
        margin-top: 8px; }
    .Btn {
      height: 54px;
      line-height: 54px;
      padding: 0 32px; }
  .ProductInfo__name + .ProductInfo__price {
    margin-top: 8px; }
  form + .ProductInfo__selects {
    margin-top: 24px; }
  .ProductInfo__selects + .ProductInfo__foot {
    margin-top: 24px; }
  .ProductInfo .itemAttention {
    margin-top: 24px; } }

@media (max-width: 919px) {
    .GlobalHeader__logo {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 54px;
      height: 35px; }
      .GlobalHeader__nav {
        display: none;
        position: absolute;
        top: 56px;
        left: 0;
        right: 0;
        width: 100%;
        padding: 20px;
        background-color: #fff; }
        .GlobalHeader__nav ul li + * {
          margin-top: 24px; }
    .GlobalFooter {
      flex-direction: column;
      padding: 32px 16px 40px; }
      .GlobalFooter__links {
        flex-direction: column;
        align-items: center; }
      .GlobalFooter__links li + * {
        margin-top: 12px; }
      .GlobalFooter__copyright {
        margin-top: 32px; }
    .SectionBasic {
      padding: 40px 12px 80px; }
    .SectionTitle + .SectionBasic__content {
      margin-top: 20px; }
    .SectionMain {
      padding: 56px 16px 80px; }
      .SectionMain__content {
        margin-top: 24px; }
  .SectionProduct {
    padding-bottom: 64px; }
    .SectionProduct__meta {
      flex-direction: column;
      padding: 32px 16px 40px; }
  .SectionProduct__info + .SectionProduct__content {
    margin-top: 24px; }
    .Btn {
      height: 44px;
      line-height: 44px;
      padding: 0 32px; }
  .ProductInfo__selects + .ProductInfo__foot {
    margin-top: 16px; }
  .ProductInfo .itemAttention {
    margin-top: 16px; } }
